@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);


body{
    background-color: #161616;
    color: #ddd;
    /* padding: 50px; */
}
h1{
    color: #eef;
    text-align: center;
}

.switch{
    padding: 5px;
    margin: 0 auto;
    background-color: rgb(22, 50, 119);
    margin-bottom: 15px;
    border-radius: 50%;
    cursor: pointer;
    width: 3%;
    text-align: center;
  }
.group{
    background: rgb(141, 141, 236);
    width: 180px;
    margin: 0 auto 20px;
    display: grid;
    grid-template-columns: 100px 80px;
    border-radius: 15px;
}
input,select{
    background: none;
    border:0;
    color:rgb(23, 22, 22);
}
input{
    padding-left: 10px;
}
select{
    padding: 15px;
}
.size{
width: 1.5rem;
margin-left: 2rem;
align-items: center;
}
.res{
    align-items: center;
    font-size: 1rem;
    margin-left: 1rem;
}
.heading{
    font-family: 'Pacifico', cursive;
    font-size: 1rem;
    display: flex;
    justify-content:space-around;
    align-items: center;
    background: #000;
    margin-bottom: 4rem;
  }
